.heading{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    font-size: 3rem;
    font-weight: 600;
    color: #ffffff;
}

.contain{
    display: flex;
    margin-top: 4rem;
    margin-left: 12rem;
}

.content{
    color: #ffffff;
    padding-left: 6rem;
    padding-top: 1rem;
}

.title{
    font-size: xx-large;
    font-weight: 700;
    padding-bottom: 1.5rem;
}

.description{
    font-size: larger;
    font-weight: 500;
    padding-left: 2rem;
    max-width: 750px;
}

.image{
    width: 250px;
    height: 250px;
    border-radius: 30px
}